@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@500;700&family=Poppins:wght@200;300;400;500;600;700&display=swap');
:root{
    --bg-black-900:#f2f2fc;
    --bg-black-100:#fdf9ff;
    --bg-black-50:#e8dfec;
    --text-black-900:#302e4d;
    --text-black-700:#504e70;
    --skin-color:#ec1839;

}
body{
    line-height: 1.5;
    font-family:'Poppins' sans-serif;
    background:#000000e3

}
.hidden{
    display: none;
}
*
{
    margin: 0;
    padding: 0;
    outline: none;
    text-decoration: none;
    box-sizing: border-box;
}
::after,::before{
    box-sizing: border-box;
}
ul{
    list-style: none;
}
.section{
    /* width:1346px; */
    height: 100%;
    padding: 0px;
    right: 0;
    z-index: 0;
    opacity: 1;
}
.pad-15{
    padding-left: 15px;
    padding-right: 15px;
}
.container{
    max-width: 1024px;
    /* margin: auto; */
    margin-left:300px;
    position:absolute;
    top: 9rem;
}
.row{
    display: flex;
    flex-wrap: wrap;
    margin-right:4rem;
    position: relative;
    margin-bottom: 5rem;

}
.shadow-dark{
    box-shadow: 0 0 20px rgba(233, 232, 237, 0.59);
}
/* ---aside --- */

.aside{
    width: 230px;
    background: var(--bg-black-900);
    position: fixed;
    display: flex;
    flex-wrap: wrap;
    left: 0;
    top: 0;
    padding: 10px;
    height: 100%;
    justify-content: center;
    align-items: center;
}
.nav{
    position: fixed;
}
.aside .logo{
    position: absolute;
    top: 43px;
    text-transform: capitalize;
}
.aside .logo a{
    color: var(--text-black-900);
    font-weight: 700;
    padding: 12px 13px;
    font-size: 17px;
    letter-spacing: 5px;
    font-style:italic;
    position: relative;
}
.aside .logo a span{
    font-family: 'Clicker Script',cursive;

}
.aside .logo a::before{
    content: '';
    position:absolute;
    width: 20px;
    min-height: 20px;
    border-top: 4px solid var(--skin-color);
    border-left: 4px solid var(--skin-color);
    top: 0;
    left: 0;

}
.aside .logo a::after{
    content: '';
    position:absolute;
    width: 20px;
    min-height: 20px;
    border-bottom: 4px solid var(--skin-color);
    border-right: 4px solid var(--skin-color);
    bottom: 1px;
    right:-0.1rem;
}
.aside .nav-toogler {
    height: 40px;
    min-width: 45px;
    border: 1px solid var(--bg-black-50);
    cursor: pointer;
    position: fixed;
    left: 300px;
    top: 20px;
    border-radius:5px;
    background: var(--bg-black-100);
    display: none;
    align-items: center;
    justify-content: center;
}
.aside .nav-toogler span{
    height: 2px;
    width: 18px;
    background: var(--skin-color);
    position:relative;

}
.aside .nav-toogler span::before{
    content: '';
    min-height: 2px;
    width: 18px;
    background: var(--skin-color);
    position:absolute;
    top: -6px;
    left: 0;

}
.aside .nav-toogler span::after{
    content: '';
    min-height: 2px;
    min-width: 18px;
    background: var(--skin-color);
    position:absolute;
    top: 6px;
    left: 0;
}
.aside .nav li{
    margin-bottom: 30px;
}
.aside .nav li a {
    font-size: 25px;
    font-weight: 400;
    border-bottom: 1px solid var(--bg-black-50);
    color: var(--text-black-900);
    padding: 10px 15px;

}
.aside .nav li a.active{
    color: var(--skin-color);
}
.aside .nav li a i{
    margin-right: 10px;
}


/* ---home section--- */

.home .home-info{
    /* max-width: 30%; */
    color: var(--bg-black-900);
}
.home .home-img{
    text-align: center;
    position: relative;
}
.home-container{
    display: flex;
}
.home-img::before{
    content: '';
    position: absolute;
    width: 60px;
    height: 60px;
    border-top: 8px solid var(--skin-color);
    border-left: 8px solid var(--skin-color);
   top: -3rem;
    left: 6rem;
}
.home-img::after{
    content: '';
    position: absolute;
    width: 60px;
    height: 60px;
    border-bottom: 8px solid var(--skin-color);
    border-right: 8px solid var(--skin-color);
    bottom: 4.1rem;
    left: 28rem;
}
.home .home-img img{
    margin-left: 150px;
    border-radius: 5px;
    height: 400px;
}
h3.hello{
    font-size: 30px;
}
h3.hello span{
    font-size: 30px;
    font-weight: 700;
   color: var(--skin-color);
}
h3.my-profession{
    font-size: 30px;
    margin: 15px 0;
    flex-wrap: wrap;
}
.typing{
    color:var(--skin-color);
}
.home-info p{
    flex-wrap: wrap;
    width:700px;
    margin-bottom: 70px;
    font-size: 25px;
    color:var(--bg-black-900);
}
.home{
    color: var(--text-black-900);
}
.btn{
    font-size: 15px;
    padding: 12px 35px;
    font-weight: 500;
    color: white;
    border-radius: 40px;
    border: none;
    background: var(--skin-color);
    transition: all 0.3s ease;
}
.btn:hover{
    transform: scale(1.05);
}


