 /* about Css End */



/* Education Css Start */
.education-exp-section{
    display: flex;
    color: var(--bg-black-100);
    flex:0 0 127%;
}
.education-container,.experience
{
    flex: 0 0 100%;
    max-width: 50%;
    margin-top: 30px;
}
.education-title,.exp-title{
    padding-left: 15px;
    padding-right: 15px;
    font-size: 30px;
    margin-bottom: 30px;
    font-weight: 700;
    color: var(--bg-black-100);
}
.timeline-container{
    padding-left: 15px;
    padding-right: 15px;
    flex: 0 0 100%;
    max-width: 90%;
}
.timeline{
    color: var(--text-black-900);
    background-color: var(--bg-black-100);
    padding: 30px 30px 30px 50px;
    border: 1px solid var(--bg-black-100);
    border-radius: 10px;
    width: 100%;
    font-size: 18px;
    position: relative;
}
.timeline-item::before{
    content: '';
    width: 2px;
    position: absolute;
    height: 92%;
    top: 40px;
    left: 20px;
    background-color: var(--skin-color);
}
.circle-dot-1{
    position: absolute;
    left: 14px;
    top: 35px;
    height: 15px;
    width: 15px;
    border-radius: 50%;
    background-color: var(--skin-color);
}
.circle-dot-2{
    position: absolute;
    left: 14px;
    top: 15.5rem;
    height: 15px;
    width: 15px;
    border-radius: 50%;
    background-color: var(--skin-color);
}
.circle-dot-3{
    position: absolute;
    left: 14px;
    top: 28.9rem;
    height: 15px;
    width: 15px;
    border-radius: 50%;
    background-color: var(--skin-color);
}
.circle-dot-4{
    position: absolute;
    left: 14px;
    top: 42.2rem;
    height: 15px;
    width: 15px;
    border-radius: 50%;
    background-color: var(--skin-color);
}
.timeline-text{
    padding-bottom: 20px;
}
.timeline-date{
    font-weight: 400;
    font-size: 16px;
    margin-bottom: 10px;
    color: var(--text-black-700);
}
.timeline-date .fa{
    margin-right: 5px;
}
.timeline-title{
    font-weight: 700;
    font-size: 24px;
    margin-bottom: 15px;
    text-transform: capitalize;
    color: var(--text-black-900);
}

